@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@200;300;400;600;700;800;900&display=swap');


$outwards-shadow:   6px 6px 10px #cecfd2,
-6px -6px 10px #ffffff;

$outwards-shadow-black:   6px 6px 6px #242424,
             -6px -6px 6px #383838;;

$inwards-shadow: inset 6px 6px 10px #cecfd2,
inset -6px -6px 10px #ffffff;


$inwards-shadow-black: inset 6px 6px 6px #0000008b,
            inset -6px -6px 6px #353535;

$dark-blue: #215288;

$clr-prim-blue: #97D1DF;

$prim-font: 'Nunito Sans', sans-serif;


.back-drop{
    position: absolute;
    height: 100vh;
    width: 100vw;
    top: 0;
    left: 0;
    background: #2727276c;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    z-index: 2;

    .progess-start{
        background: rgb(212, 212, 212);
        width: 80%;
        height: 10px;
        overflow: hidden;
        border-radius: 100px;


        div {
            background: linear-gradient(100deg, #2374d2 0%, #37ff96 100%);
            height: 10px;
            border-radius: 100px;       
            transition: .15s cubic-bezier(.05,.71,.71,.68);
        }
    }

    .start-form-container {
        background: #f2f3f7;
        border-radius: 24px;
        padding: 2em;
        width: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        input{
         border-radius: 74px;
        background: #f2f3f7;
        box-shadow:  6px 6px 10px #cecfd2,
         -6px -6px 10px #ffffff;
        margin: 1em 0;
        font-size: 1.5rem;
        padding: .8rem;
        color: $clr-prim-blue;
        margin-left: auto;
        margin-right: auto;
    }
    ::placeholder{
       color:  $clr-prim-blue;
        opacity: 50%;
        }

        .body-start{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            min-height: 300px;
            box-shadow: $inwards-shadow;
            border-radius: 24px;
            width: 90%;
            
            div{
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
            }
        }

        .footer-start{
            display: flex;
            width: 80%;
            justify-content: space-evenly;

            button{
                margin: 1em;
            }
        }

    }
}


.close-btn-form {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

button{

    &:disabled {
    opacity: 50%;
  }
}